$phoneMax: 480px;
$phoneMin: 481px;
//******/
$tabletMax: 768px;
$tabletMin: 769px;

$desctopMax: 1199px;
$desctopMin: 1200px;

/**
//* Mobile
*/
@mixin maxW-Phone {
    @media screen and (max-width: $phoneMax) {
        @content;
    }
}

@mixin minW-Phone {
    @media screen and (min-width: $phoneMin) {
        @content;
    }
}

/**
//* Tablet
*/

@mixin maxW-Tablet {
    @media screen and (max-width: $tabletMax) {
        @content;
    }
}

@mixin minW-Tablet {
    @media screen and (min-width: $tabletMin) {
        @content;
    }
}

/**
//* Desctop
*/

@mixin maxW-Dt {
    @media screen and (max-width: $desctopMax) {
        @content;
    }
}

@mixin minW-Dt {
    @media screen and (min-width: $desctopMin) {
        @content;
    }
}

