@mixin gridBlock($cols, $gapRow, $gapCol) {

    display: grid;
    grid-template-columns: repeat($cols, 1fr);

    @if ($gapCol) {
        grid-gap: #{$gapRow + px} #{$gapCol + px};
    }

    @else {
        grid-gap: #{$gapRow + px}
    }
}

